.footer {
	padding-top: 6rem;
	padding-bottom: 6rem;
	background: $violet;
	@media (max-width: 573px) {
		padding-top: 3rem;
		padding-bottom: 2rem
	}
	.container {
		display: flex;
		justify-content: space-between;
		font-weight: 500;
		@media (max-width: 573px) {
			flex-wrap:wrap;
			font-weight:400;
		}
	}
	.footer__block {
		&.footer__block_block1 {
			width: 45.2rem;
			@media (max-width: 573px) {
				margin-bottom:1rem;
			}
		}
		&.footer__block_block2 {
			width: 27.8rem;
			@media (max-width: 573px) {
				display:none;
			}
		}
		&.footer__block_block3 {
			width: 27rem;
		}
		.footer__logo {
			margin-bottom: 3.7rem;
			@media (max-width: 573px) {
				margin-bottom:2rem;
			}
			img {
				max-width: 26.4rem;
				@media (max-width: 573px) {
					max-width: 20.7rem;
				}
			}
		}
		.footer__soc {
			display: flex;
			flex-wrap: wrap;
			margin-bottom: 3.2rem;
			@media (max-width: 573px) {
				margin-bottom:1.6rem;
			}
			a {
				margin-right: 2.5rem;

				img {
					max-width: 1.6rem;
					max-height: 1.6rem;
				}
				svg {
					max-width: 1.6rem;
					max-height: 1.6rem;
					path {
						@include tr;
					}
				}
				&:hover {
					svg {
						path {
							fill: rgba(96, 30, 243, 1);
						}
					}
				}
			}
		}
		.footer__caption {
			margin-bottom: 2.3rem;
			font-size: 2rem;
			// font-weight: bold;
			color: #272727;
			letter-spacing: 1.18789px;
			text-transform: uppercase;
			@media (max-width: 573px) {
				display:none;
			}
		}
		.footer__text {
			font-size: 1.5rem;
			line-height: 2.3rem;
			letter-spacing: 1.18789px;
			@media (max-width: 573px) {
				font-size: 1.3rem;
				line-height: 1.6rem;;
			}
			span {
				display: block;
				margin-bottom: 1.7rem;
				// font-weight:bold;
				@media (max-width: 573px) {
					display:none;
				}
			}
			p {
				margin-bottom: 2.3rem;
				@media (max-width: 573px) {
					margin-bottom:1.1rem;
				}
			}
			a {
				@include tr;
				border-bottom: 1px solid $black;
				&:hover {
					color: $dark-violet;
					// text-decoration: underline;
					border-bottom: none;
				}
			}
		}
		ul {
			@include ulClean;
			margin-bottom: 2.3rem;
			li {
				a {
					font-size: 1.5rem;
					line-height: 2.3rem;
					letter-spacing: 1.18789px;
					color: $black;
					@include tr;
					&:hover {
						color: $dark-violet;
					}
				}
			}
		}
	}
}
