.h1-title {
  margin: 0;
  padding: 0;
  font-weight: bold;
  font-size: 2.5rem;
  line-height: 3.7rem;
  letter-spacing: 0.187895px;
}

@media (max-width: 573px) {
  .h1-title {
    font-size: 2.4rem;
    line-height: 2.8rem;
    letter-spacing: 0.616654px;
  }
}

h2 {
  font-weight: bold;
  font-size: 3rem;
  line-height: 3.7rem;
  margin: 0;
  padding: 0;
  margin-bottom: 4.5rem;
  letter-spacing: 1.18789px;
}

@media (max-width: 573px) {
  h2 {
    font-size: 2.4rem;
    line-height: 1.2em;
  }
}

.h2-title {
  font-weight: bold;
  font-size: 3rem;
  line-height: 3.7rem;
  margin: 0;
  padding: 0;
  margin-bottom: 4.5rem;
  letter-spacing: 1.18789px;
}

@media (max-width: 573px) {
  .h2-title {
    font-size: 2.4rem;
    line-height: 1.2em;
    letter-spacing: 0.616654px;
    margin-bottom: 3rem;
  }
}

.h2-title_margin {
  margin-bottom: 2.5rem;
}

@media (max-width: 573px) {
  .h2-title_margin {
    margin-bottom: 1rem;
  }
}

.h2-title.violet {
  color: #601ef3;
}

.h2-title.icon_count {
  padding-left: 6.6rem;
  position: relative;
}

@media (max-width: 573px) {
  .h2-title.icon_count {
    padding-left: 4rem;
  }
}

.h2-title.icon_count:before {
  content: "";
  width: 4.2rem;
  height: 4.2rem;
  background: #601ef3;
  border-radius: 50%;
  position: absolute;
  left: 0;
  top: -0.2rem;
  z-index: 1;
}

@media (max-width: 573px) {
  .h2-title.icon_count:before {
    width: 2.3rem;
    height: 2.3rem;
  }
}

.h2-title.icon_count:after {
  content: attr(data-count);
  color: #fff;
  width: 4.2rem;
  height: 4.2rem;
  position: absolute;
  z-index: 2;
  left: 0;
  top: -0.2rem;
  font-size: 2.3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  letter-spacing: normal;
}

@media (max-width: 573px) {
  .h2-title.icon_count:after {
    width: 2.3rem;
    height: 2.3rem;
    font-size: 1.3rem;
  }
}

.h2-title.icon_heart {
  padding-left: 6.6rem;
  background: url("../img/purple_heart.png") top left no-repeat;
  background-size: 4.6rem;
  background-size: 3.8rem;
}

@media (max-width: 573px) {
  .h2-title.icon_heart {
    padding-left: 4rem;
    background-size: 2.4rem;
  }
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 23rem;
  background: #ffcc50;
  border-radius: 0.6rem;
  height: 4.7rem;
  font-size: 1.7rem;
  text-transform: uppercase;
  letter-spacing: 0.490667px;
  font-weight: bold;
  -webkit-transition: all .25s ease;
  transition: all .25s ease;
  color: #000;
  margin-left: auto;
  margin-right: auto;
  margin-top: 5rem;
  margin-bottom: 5rem;
  border: none;
  cursor: pointer;
}

@media (max-width: 573px) {
  .button {
    font-size: 1.3rem;
  }
}

.button_margin {
  margin-top: 0;
  margin-bottom: 0;
}

.button_big {
  width: 46rem;
}

@media (max-width: 573px) {
  .button_big {
    width: 100%;
  }
}

.button_middle {
  width: 100%;
  height: 4.6rem;
  margin: 0;
  font-size: 1.2rem;
  line-height: 1.6rem;
}

.button:hover {
  background: #ffb500;
}

.select {
  display: inline-block;
  vertical-align: top;
  position: relative;
  width: 100%;
}

.select__gap {
  border: 1px solid #cfcfcf;
  border-radius: 1rem;
  background: #f9fdff;
  color: #8f9295;
  font-size: 1.2rem;
  padding: 1.2rem 1.5rem;
  line-height: 2rem;
  cursor: pointer;
  position: relative;
  z-index: 3;
}

@media (max-width: 573px) {
  .select__gap {
    padding: 0.6rem 1.5rem;
  }
}

.select__gap.bolder {
  font-weight: 500;
  color: #000;
}

.on.select__gap,
.select__gap:hover {
  color: #000;
}

.select__list {
  background: transparent;
  margin: 0px 0;
  position: absolute;
  background: #f9fdff;
  border-radius: 0rem 0rem 1rem 1rem;
  z-index: 2;
