.h1-title {
    margin: 0;
    padding: 0;
    font-weight: bold;
    font-size: 2.5rem;
    line-height: 3.7rem;
    letter-spacing: 0.187895px;
    @media (max-width:573px) {
        font-size: 2.4rem;
        line-height: 2.8rem;
        letter-spacing: 0.616654px;
    }
    
}
h2 {
    font-weight: bold;
    font-size: 3rem;
    line-height: 3.7rem;
    margin: 0;
    padding: 0;
    margin-bottom: 4.5rem;
    letter-spacing: 1.18789px;
    @media (max-width:573px) {
        font-size: 2.4rem;
        line-height: 1.2em;
    }
}
.h2-title{
    font-weight: bold;
    font-size: 3rem;
    line-height: 3.7rem;
    margin: 0;
    padding: 0;
    margin-bottom: 4.5rem;
    letter-spacing: 1.18789px;
    @media (max-width:573px) {
        font-size: 2.4rem;
        line-height: 1.2em;
        letter-spacing: 0.616654px;
        margin-bottom:3rem;
    }
    &_margin{
        margin-bottom: 2.5rem;
        @media (max-width:573px) {
            margin-bottom:1rem;
        }
    }
    &.violet {
        color: $dark-violet;
    }
    &.icon_count {
        padding-left: 6.6rem;
        position: relative;
        @media (max-width:573px) {
            padding-left:4rem;
        }

        &:before {
            content: "";
            width: 4.2rem;
            height: 4.2rem;
            background: $dark-violet;
            border-radius: 50%;
            position: absolute;
            left: 0;
            top: -0.2rem;
            z-index: 1;
            @media (max-width:573px) {
                width: 2.3rem;
                height: 2.3rem;
            }
        }
        &:after {
            content: attr(data-count);
            color: #fff;
            width: 4.2rem;
            height: 4.2rem;
            position: absolute;
            z-index: 2;
            left: 0;
            top: -0.2rem;
            font-size: 2.3rem;
            @include flexcc;
            letter-spacing: normal;
            @media (max-width:573px) {
                width: 2.3rem;
                height: 2.3rem;
                font-size: 1.3rem;
            }
        }
    }
    &.icon_heart {
        padding-left: 6.6rem;
        background: url("../img/purple_heart.png") top left no-repeat;
        background-size: 4.6rem;
        background-size: 3.8rem;
        @media (max-width:573px) {
            padding-left:4rem;
            background-size:2.4rem;
        }
    }
}