.header {
	background: $white;
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.07);
	position: fixed;
	width: 100%;
	z-index: 999;
	position: fixed;
	top: 0px;
	.container {
		display: flex;
		align-items: center;
		@media (max-width: 573px) {
			justify-content: center;
			flex-wrap: wrap;
		}
	}
	&__logo {
		padding-top: 2.4rem;
		padding-bottom: 0.3rem;
		display: block;
		img {
			width: 21.1rem;
		}
		@media (max-width: 573px) {
			padding-top: 0.8rem;
			img {
				max-width: 11rem;
			}
		}

		h1, span {
			margin:0;
			padding:0;
			font-weight: 600;
			font-size: 1.4rem;
			line-height: 2.7rem;
			letter-spacing: 0.187895px;
			color: $black;
			// font-family: "Montserrat Alternates", sans-serif;
			display: block;
			padding-left: 6rem;
			font-weight: 400;
			padding-bottom: 0.4rem;
			@media (max-width: 573px) {
				font-size: 0.8rem;
				// line-height: 1.9rem;
				line-height: 1em;
				padding-left: 3.1rem;
			}
		}
	}
	&__rss_violet {
		background: $violet;
		padding: 1.3rem 1.5rem;
		text-align: left;
		width: 100%;
		display: none;
		@media (max-width: 573px) {
			display: block;
		}
		a {
			font-weight: bold;
			font-size: 1.2rem;
			line-height: 1.6rem;
			text-transform: uppercase;
			display: block;
			&:hover,
			&:active {
				color: #601ef3;
			}
		}
	}
	&__soc {
		margin-left: auto;
		width: 32.2rem;
		height: 6.4rem;
		@include flexcc;
		background: #f5fbff;
		box-shadow: 0px 1.3px 1.3rem rgba(0, 0, 0, 0.1);
		border-radius: 3.2rem;
		padding: 0 2.7rem;
		justify-content: space-between;
	}
	&__search-icon {
		margin-left: auto;
		cursor: pointer;
		max-width: 2.2rem;
		@media (max-width: 573px) {
			position: absolute;
			right: 6.1rem;
			top: 5.6rem;
			img {
				max-width: 1.5rem;
				max-height: 1.5rem;
				display: block;
			}
		}
	}

	&__hamburger {
		position: relative;
		margin-left: 7.4rem;
		margin-bottom: 0.5rem;
		@media (max-width: 573px) {
			position: absolute;
			right: 1.5rem;
			top: 5.6rem;
			margin-bottom: 0rem;
		}
		.header__menu-btn {
			display: flex;
			align-items: center;
			position: relative;
			width: 2.4rem;
			height: 1.6rem;
			cursor: pointer;
			z-index: 1;
			@media (max-width: 916px) {
				width: 1.6rem;
				height: 1.2rem;
			}
		}
		.header__menu-btn > span,
		.header__menu-btn > span::before,
		.header__menu-btn > span::after {
			display: block;
			position: absolute;

			width: 100%;
			height: 2px;

			background-color: #000;

			transition-duration: 0.25s;
		}
		.header__menu-btn > span::before {
			content: "";
			top: -0.8rem;
			@media (max-width: 916px) {
				top: -0.5rem;
			}
		}
		.header__menu-btn > span::after {
			content: "";
			top: 0.8rem;
			@media (max-width: 916px) {
				top: 0.5rem;
			}
		}
		&.open {
			.header__menu-btn > span {
				transform: rotate(45deg);
			}
			.header__menu-btn > span::before {
				top: 0;
				transform: rotate(0);
			}
			.header__menu-btn > span::after {
				top: 0;
				transform: rotate(90deg);
			}
		}
	}
}

.header__menu-inner {
	border-top: 1px solid #e7e7e7;
	padding-top: 2.6rem;
	// padding-bottom:3rem;
	display: none;
	// @media (max-width:916px) {
	// 	padding-top:1.3rem;
	// }
	&.open {
		display: block;
	}
	@media (max-width: 573px) {
		background: $violet;
		height: 100vh;
		padding-bottom:6rem;
		overflow-y: scroll;
	}
	.container {
		display: flex;
		flex-wrap: wrap;
		.header__column {
			// min-width: 49.4rem;
			display: flex;
			flex-wrap: wrap;
			width: 50%;
			@media (max-width: 573px) {
				min-width: auto;
				width: 100%;
				margin-bottom: 5rem;
			}

			.header__caption {
				font-size: 1.7rem;
				line-height: 3.7rem;
				text-transform: uppercase;
				margin-bottom: 0.8rem;
				letter-spacing: 1.18789px;
				font-weight: 500;
				width: 100%;

			}
			ul {
				@include ulClean;
				margin-right: 7.8rem;
				display: flex;
				flex-wrap: wrap;
				@media (max-width: 573px) {
					margin-right: 0px;
					width: 100%;
					display: flex;
					flex-wrap: wrap;
				}

				li {
					font-size: 1.7rem;
					line-height: 3.7rem;
					letter-spacing: 1.18789px;
					width: 50%;
					@media (max-width: 573px) {
						width: 50%;
					}

					a {
						@include tr;
						color: rgba(152, 152, 152, 1);
						img{
							display:none;
						}
						@media (max-width: 573px) {
							color: #000000;
						}
						&:hover {
							color: rgba(96, 30, 243, 1);
						}
					}
				}
				@media (max-width: 573px) {
				&.header__tags{
					display:block;
					li {
						width:100%;
						margin-bottom:1.9rem;
						a{
							background: #FFFFFF;
							box-shadow: 0px 1.3px 13px rgba(0, 0, 0, 0.06);
							border-radius: 34px;
							display:inline-flex;
							align-items:center;
							padding:0 1.6rem;
							img {
								margin-left: 1.6rem;
								max-width: 3.2rem;
								max-height: 3.2rem;
								display:block;
							}
						}
					}
				}
			}
			}
		}
		.header__link-main {
			padding-top: 1.7rem;
			padding-bottom: 3.4rem;

			@media (max-width: 573px) {
				display: none;
			}
			a {
				display: flex;
			align-items:center;
				font-size: 1.7rem;
				line-height: 3.7rem;
				text-align: center;
				letter-spacing: 0.69px;
				text-transform: uppercase;
				color: $black;
				font-weight: 500;
				// padding-right: 1.3rem;
				// background: url("../img/angle-right.svg") right center no-repeat;
				// background-size: 0.8rem;
				@include tr;
				svg{
					margin-left: 0.6rem;
					max-width:0.8rem;
					path{
						@include tr;
					}
				}

				&:hover {
					color: rgba(96, 30, 243, 1);
					svg{
						path{
							fill:rgba(96, 30, 243, 1);
						}
					}
				}
			}
		}
	}

	.header__rss {
		padding-top: 1.2rem;
		padding-bottom: 1.2rem;
		background: #ffb500;

		@media (max-width: 573px) {
			display: none;
		}
		a {
			display: flex;
			align-items:center;
			font-size: 1.7rem;
			line-height: 3.4rem;
			text-align: center;
			letter-spacing: 0.69px;
			text-transform: uppercase;
			color: $black;
			font-weight: 500;
			// padding-right: 1.3rem;
			// background: url("../img/angle-right.svg") right center no-repeat;
			// background-size: 0.8rem;
			@include tr;
			svg{
				margin-left: 0.6rem;
				max-width:0.8rem;
				path{
					@include tr;
				}
			}
			&:hover {
				color: rgba(96, 30, 243, 1);
				svg{
					path{
						fill:rgba(96, 30, 243, 1);
					}
				}
			}
		}
	}
}
.menu {
	margin-bottom: 1.6rem;
	padding-top: 12.9rem;

	@media (max-width: 573px) {
		display: none;
	}
	ul {
		@include ulClean;
		@include flexcc;
		li {
			margin: 0 2.4rem;

			a {
				display: block;
				font-weight: 500;
				font-size: 1.7rem;
				line-height: 3.7rem;
				letter-spacing: 1.18789px;
				color: $black;
				font-family: $font1;

				@include tr;
				&.active,
				&:hover {
					color: $dark-violet;
				}
			}
		}
	}
}
.tags {
	margin-bottom: 5.5rem;
	@media (max-width: 573px) {
		display: none;
	}
	.container {
		display: flex;
		justify-content: space-between;
		a {
			@include flexcc;
			background: $white;
			box-shadow: 0px 1.3px 1.3rem rgba(0, 0, 0, 0.1);
			border-radius: 3.4rem;
			padding: 0.5rem 1.6rem 0.5rem 1.6rem;
			font-size: 1.7rem;
			line-height: 2.7rem;
			font-family: $font1;
			font-weight: 500;
			letter-spacing: 1.18789px;
			@include tr;

			img {
				margin-left: 1.6rem;
				max-width: 3.2rem;
				max-height: 3.2rem;

			}
			&:hover,
			&.active {
				&:nth-child(1),
				&:nth-child(2) {
					background: #d3f2ff;
					box-shadow: 0px 1.3px 1.3rem rgba(0, 0, 0, 0.2);
				}
				&:nth-child(3),
				&:nth-child(4),
				&:nth-child(5) {
					background: #d7d1ff;
					box-shadow: 0px 1.3px 1.3rem rgba(0, 0, 0, 0.2);
				}
			}
		}
	}
}
.search-popup {
	width: 100vw;
	height: 100vh;
	@include flexcc;
	position: fixed;
	left: 0;
	top: 0;
	background: $violet;
	z-index: 10000;
	display: none;
	&.open {
		display: flex;
	}
	.search-popup__close-btn {
		height: 2.4rem;
		width: 2.4rem;
		position: absolute;
		top: 4.1rem;
		right: 50%;
		margin-right: -57.3rem;
		z-index: 1001;
		cursor: pointer;
		@media (max-width: 573px) {
			margin-right: 0px;
			top: 1.7rem;
			right: 1.7rem;
			height: 1.4rem;
			width: 1.4rem;
		}
	}
	.search-popup__close-btn > span,
	.search-popup__close-btn > span::before,
	.search-popup__close-btn > span::after {
		display: block;
		position: absolute;

		width: 100%;
		height: 2px;

		background-color: #000;

		transition-duration: 0.25s;
	}
	.search-popup__close-btn > span::before {
		content: "";
		top: -0.8rem;
		// @media (max-width:916px) {
		// 	top:-0.6rem;
		// }
	}
	.search-popup__close-btn > span::after {
		content: "";
		top: 0.8rem;
		// @media (max-width:916px) {
		// 	top:0.6rem;
		// }
	}
	.search-popup__close-btn > span {
		transform: rotate(45deg);
	}
	.search-popup__close-btn > span::before {
		top: 0;
		transform: rotate(0);
	}
	.search-popup__close-btn > span::after {
		top: 0;
		transform: rotate(90deg);
	}

	.search-popup__form {
		width: 72.1rem;
		@media (max-width: 573px) {
			width: 100%;
			padding: 0 1.5rem;
		}
		::-webkit-input-placeholder {
			// font-weight: bold;
			font-family: "Gotham Pro";
			font-size: 3rem;
			line-height: 3.7rem;
			color: #828184;
			@media (max-width: 573px) {
				font-size: 1.9rem;
				line-height: 2.6rem;
			}
		}
		::-moz-placeholder {
			// font-weight: bold;
			font-family: "Gotham Pro";
			font-size: 3rem;
			line-height: 3.7rem;
			color: #828184;
			@media (max-width: 573px) {
				font-size: 1.9rem;
				line-height: 2.6rem;
			}
		}
		:-ms-input-placeholder {
			// font-weight: bold;
			font-family: "Gotham Pro";
			font-size: 3rem;
			line-height: 3.7rem;
			color: #828184;
			@media (max-width: 573px) {
				font-size: 1.9rem;
				line-height: 2.6rem;
			}
		}
		:-moz-placeholder {
			// font-weight: bold;
			font-family: "Gotham Pro";
			font-size: 3rem;
			line-height: 3.7rem;
			color: #828184;
			@media (max-width: 573px) {
				font-size: 1.9rem;
				line-height: 2.6rem;
			}
		}
		form {
			display: flex;
			border-bottom: 2px solid #000;
			input[type="text"] {
				flex: 1;
				border: none;

				height: 4.2rem;
				padding: 0 3px;
				font-size: 3rem;
				letter-spacing: 0.187895px;
				color: $black;
				background: none;
				@media (max-width: 573px) {
					font-size: 1.9rem;
					height: 2.6rem;
				}
			}
			input[type="submit"] {
				width: 2.9rem;
				height: 4.2rem;
				background: url("../img/search_button.svg") center center no-repeat;
				border: none;
				background-size: contain;
				cursor:pointer;
				@media (max-width: 573px) {
					height: 2.6rem;
					width: 1.9rem;
					background-size: contain;
				}
			}
		}
	}
}
