@import "import/reset";
@import "import/fonts";
@import "import/variables";
@import "import/titles";
@import "import/forms";
@import "import/header";
@import "import/footer";

.content-blog {
	&.content_with-padding {
		padding-top: 12.6rem;
		@media (max-width: 573px) {
			padding-top: 0px;
		}
	}
	.content__text-intro {
		margin-bottom: 4.5rem;
		@media (max-width: 573px) {
			margin-bottom: 3rem;
		}
		h2 {
			margin-bottom: 1.2rem;
		}
		p {
			font-size: 1.8rem;
			line-height: 2.5rem;
			letter-spacing: 0.49px;
			@media (max-width: 573px) {
				line-height: 2.1rem;
			}
		}
	}

	.two-items {
		margin-bottom: 6.9rem;
		display: grid;
		grid-template-columns: 1fr 1fr;
		grid-column-gap: 3rem;
		@media (max-width: 573px) {
			display: none;
		}
		&__item {
			display: flex;
			align-items: flex-end;
			height: 42rem;
			border-radius: 1rem;
			background-size: cover;
			position: relative;
			padding: 5.7rem 7rem 3rem 2rem;
			@include tr;
			&:hover {
				box-shadow: 0px 0px 1.8rem rgba(0, 0, 0, 0.2);
			}
			&:before {
				content: "";
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0.85) 100%);
				z-index: 1;
				border-radius: 1rem;
			}
		}
		&__desc {
			position: relative;
			z-index: 2;
		}
		&__like-tag {
			font-weight: bold;
			font-weight: 500;
			font-size: 1.5rem;
			line-height: 1em;
			padding: 0.9rem 1.4rem;
			background: $white;
			border: 1px solid #373737;
			box-shadow: 0px 0px 1.8rem rgba(0, 0, 0, 0.07);
			border-radius: 2rem;
			display: inline-flex;
			text-transform: uppercase;
			margin-bottom: 1.4rem;
		}
		&__name {
			font-weight: bold;
			font-weight: 500;
			font-size: 2.2rem;
			line-height: 2.9rem;
			letter-spacing: 0.490667px;
			color: $white;
		}
	}
	.whats-new {
		margin-bottom: 7.7rem;
		@media (max-width: 573px) {
			display: none;
		}
		&__items {
			display: flex;
		}
		&__img {
			width: 15rem;
			height: 15rem;
			margin-bottom: 3.3rem;
			border-radius: 50%;
			background-size: cover;
		}
		&__name {
			font-weight: bold;
			font-weight: 500;
			font-size: 1.5rem;
			line-height: 1.9rem;
			letter-spacing: 0.490667px;
			color: #4a4a4a;
			font-family: $font1;
			@include tr;
		}
		&__item {
			display: block;
			width: 25%;
			padding-top: 2.4rem;
			padding-bottom: 3rem;
			padding-left: 2.1rem;
			padding-right: 2.1rem;
			border-top: 1px solid #e7e7e7;
			border-right: 1px solid #e7e7e7;
			&:last-child {
				border-right: none;
			}
			&:hover {
				color: $black;
				.whats-new__name {
					color: $black;
				}
			}
		}
	}
	.hr-choise {
		padding-bottom: 6.9rem;
		@media (max-width: 573px) {
			padding-bottom: 4.5rem;
		}

		.owl-carousel .owl-nav button.owl-next.disabled,
		.owl-carousel .owl-nav button.owl-prev.disabled {
			display: none;
		}
		.owl-carousel .owl-nav button.owl-next {
			width: 3.8rem;
			height: 3.8rem;
			background: url("../img/arrow-right.svg") center center no-repeat;
			border-radius: 50%;
			position: absolute;
			z-index: 5;
			// top:50%;
			// margin-top:-1.9rem;
			// right:-4.4rem;
			background-size: 1.1rem;
			bottom: 4.5rem;
			right: 1rem;
			background-color: #4d4d4d;
			box-shadow: 0px 1.3px 1.3rem rgba(0, 0, 0, 0.1);
			@include tr;
			&:hover {
				background-color: #313131;
				box-shadow: 0px 1.3px 1.3rem rgba(183, 183, 183, 0.1);
			}
			@media (max-width: 573px) {
				display: none;
			}
		}
		.owl-carousel .owl-nav button.owl-prev {
			width: 3.8rem;
			height: 3.8rem;
			background: url("../img/arrow-left.svg") center center no-repeat;
			border-radius: 50%;
			position: absolute;
			z-index: 5;
			// top:50%;
			// margin-top:-1.9rem;
			bottom: 4.5rem;
			left: 1rem;
			background-size: 1.1rem;
			background-color: #4d4d4d;
			box-shadow: 0px 1.3px 1.3rem rgba(0, 0, 0, 0.1);
			@include tr;
			&:hover {
				background-color: #313131;
				box-shadow: 0px 1.3px 1.3rem rgba(183, 183, 183, 0.1);
			}
			@media (max-width: 573px) {
				display: none;
			}
		}
		.owl-carousel .owl-stage-outer {
			padding-top: 2rem;
			padding-bottom: 2rem;
			@media (max-width: 573px) {
				padding: 0;
			}
		}
		&__slide {
			a {
				display: flex;
				height: 42rem;
				width: 55.8rem;
				margin-left: 0.6rem;
				margin-right: 1.4rem;
				background: $white;

				box-shadow: 0px 0px 1.8rem rgba(0, 0, 0, 0.07);
				border-radius: 1rem;
				@media (max-width: 573px) {
					display: block;
					background: none;
					box-shadow: none;
					height: auto;
					width: 100%;
					// width: 18.3rem;
					margin-right: 2rem;
					margin-bottom: 5rem;
				}
			}
		}
		&__img {
			width: 50%;
			background-size: cover;
			border-radius: 1rem 0 0 1rem;
			@media (max-width: 573px) {
				width: 7.8rem;
				height: 7.8rem;
				border-radius: 50%;
				margin-bottom: 1.8rem;
			}
		}
		&__desc {
			padding: 2.6rem 1.7rem;
			width: 50%;
			@media (max-width: 573px) {
				width: auto;
				padding: 0px;
			}
		}
		&__params {
			height: 3.5rem;
			margin-bottom: 2.7rem;
			@media (max-width: 573px) {
				display: none;
			}
		}
		&__smile-icon {
			width: 5.2rem;
			height: 3.5rem;
			display: flex;
			align-items: center;
			justify-content: center;
			background: #b3a5ff;
			box-shadow: 0px 1.3px 1.3rem rgba(0, 0, 0, 0.1);
			border-radius: 3.2rem;
			img {
				max-width: 2rem;
			}
		}

		&__name {
			font-weight: bold;
			font-size: 1.8rem;
			line-height: 2.5rem;
			letter-spacing: 0.490667px;
			margin-bottom: 3rem;
			@media (max-width: 573px) {
				line-height: 2rem;
				margin-bottom: 0px;
			}
		}
		&__text {
			font-size: 1.8rem;
			line-height: 2.5rem;
			letter-spacing: 0.490667px;
			@media (max-width: 573px) {
				display: none;
			}
		}
	}
	.hr-bank {
		padding-bottom: 6.8rem;

		@media (max-width: 573px) {
			padding-bottom: 4rem;
		}
		.owl-carousel .owl-nav button.owl-next.disabled,
		.owl-carousel .owl-nav button.owl-prev.disabled {
			display: none;
		}
		.owl-carousel .owl-nav button.owl-next {
			width: 3.8rem;
			height: 3.8rem;
			background: url("../img/arrow-right.svg") center center no-repeat;
			border-radius: 50%;
			position: absolute;
			z-index: 5;
			// top:50%;
			// margin-top:-1.9rem;
			background-size: 1.1rem;
			top: 8.1rem;
			right: 1rem;
			background-color: #4d4d4d;
			box-shadow: 0px 1.3px 1.3rem rgba(0, 0, 0, 0.1);
			@include tr;
			&:hover {
				background-color: #313131;
				box-shadow: 0px 1.3px 1.3rem rgba(183, 183, 183, 0.1);
			}
			@media (max-width: 573px) {
				display: none;
			}
		}
		.owl-carousel .owl-nav button.owl-prev {
			width: 3.8rem;
			height: 3.8rem;
			background: url("../img/arrow-left.svg") center center no-repeat;
			border-radius: 50%;
			position: absolute;
			z-index: 5;
			// top:50%;
			// margin-top:-1.9rem;
			top: 8.1rem;
			left: 1rem;
			background-color: #4d4d4d;
			box-shadow: 0px 1.3px 1.3rem rgba(0, 0, 0, 0.1);
			background-size: 1.1rem;
			@include tr;
			&:hover {
				background-color: #313131;
				box-shadow: 0px 1.3px 1.3rem rgba(183, 183, 183, 0.1);
			}
			@media (max-width: 573px) {
				display: none;
			}
		}
		.owl-carousel .owl-stage-outer {
			padding-top: 2rem;
			padding-bottom: 2rem;
		}
		&__slide {
			width: 16.6rem;
			margin-right: 2.9rem;
			box-shadow: 0px 0px 1.8rem rgba(0, 0, 0, 0.07);
			border-radius: 1rem;

			@media (max-width: 573px) {
				width: 18.3rem;
				margin-right: 2rem;
			}

			@include tr;
			&:hover {
				box-shadow: 0px 0px 1.8rem rgba(0, 0, 0, 0.2);
			}
			&_violet {
				background: linear-gradient(180deg, #b3a5ff 0%, #8e79ff 100%);
			}
			&_yellow {
				background: linear-gradient(180deg, #ffdd85 0%, #fabb42 64.16%);
			}
			&_blue {
				background: linear-gradient(180deg, #aad6ff 0%, #57cdff 100%);
			}
			a {
				display: flex;
				flex-direction: column;
				height: 33.8rem;
				padding: 1.1rem;
				padding-bottom: 3.5rem;
				padding-right: 4rem;
				align-items: flex-start;
				position: relative;
				justify-content: space-between;
				border-radius: 1rem;
				background-position: center center;
				@media (max-width: 573px) {
					padding: 1.8rem 0.8rem;
					justify-content: flex-end;
				}
				&:before {
					content: "";
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, $black 100%);
					z-index: 1;
					border-radius: 1rem;
				}
			}

			&_violet,
			&_yellow,
			&_blue {
				a:before {
					display: none;
				}
			}
		}
		&__like-tag {
			display: inline-flex;
			background: $white;
			font-weight: 500;
			font-size: 1.5rem;
			line-height: 1em;
			padding: 0.9rem 1rem;
			letter-spacing: 0.0069em;
			text-transform: uppercase;
			color: #272727;
			border: 1px solid #373737;
			box-sizing: border-box;
			box-shadow: 0px 0px 1.8rem rgba(0, 0, 0, 0.07);
			border-radius: 2rem;
			position: relative;
			z-index: 2;
			@media (max-width: 573px) {
				display: none;
			}
		}
		&__desc {
			font-weight: 500;
			font-size: 1.3rem;
			line-height: 1.9rem;
			letter-spacing: 0.490667px;
			color: $white;
			position: relative;
			z-index: 2;
			@media (max-width: 573px) {
				font-size: 1.6rem;
				line-height: 2rem;
				font-weight: 400;
			}
		}
		&__name {
			margin-bottom: 1rem;
			@media (max-width: 573px) {
				font-size: 1.8rem;
				font-weight: 500;
			}
		}
	}
	.subscribe {
		background: #fff8cc;
		box-shadow: 0px 0px 1.8rem rgba(0, 0, 0, 0.07);
		border-radius: 1rem;
		padding: 2.1rem 1.7rem;
		&_blue {
			background: $blue;
		}
		&_violet {
			background: #d7d1ff;
		}
		&__name {
			font-weight: bold;
			font-size: 1.6rem;
			line-height: 1.9rem;
			letter-spacing: 0.490667px;
			margin-bottom: 1.1rem;
			@media (max-width: 573px) {
				font-size: 1.8rem;
				line-height: 2.1rem;
			}
		}
		&__desc {
			font-size: 1.6rem;
			line-height: 1.9rem;
			letter-spacing: 0.490667px;
			margin-bottom: 1.9rem;
			@media (max-width: 573px) {
				font-size: 1.8rem;
				line-height: 2.1rem;
			}
		}

		&__input {
			display: block;
			width: 100%;
			height: 4.5rem;
			margin-bottom: 1.3rem;
			// text-align:center;
			font-size: 1.2rem;
			// font-weight:500;
			letter-spacing: 0.490667px;
			color: $black;
			padding: 0 1.5rem;
			background: rgba(255, 255, 255, 0.68);
			border: 1px solid #cfcfcf;
			border-radius: 1rem;
		}
		&__form-text {
			font-style: italic;
			font-weight: normal;
			font-size: 0.9rem;
			line-height: 1.2rem;
			letter-spacing: 0.490667px;
			// padding-left:3.4rem;
			// padding-right:3.4rem;
			// margin-bottom:1.1rem;
		}
		input[type="submit"] {
