			// grid-column-start: 1;
			// grid-column-end: 3;
			grid-row: span 1;
			grid-column: span 2;
			height: 42rem;
			box-shadow: 0px 0px 1.8rem rgba(0, 0, 0, 0.07);
			border-radius: 1rem;
			@include tr;
			@media (max-width: 573px) {
				grid-column-end: 1;
			}
			&:hover {
				box-shadow: 0px 0px 1.8rem rgba(0, 0, 0, 0.2);
			}
