	.show-more {
		@include flexcc;
		height: 4.7rem;
		width: 26.5rem;
		background: $white;
		box-shadow: 0px 0px 1.8rem rgba(0, 0, 0, 0.07);
		border-radius: 0.6rem;
		font-weight: bold;
		font-size: 1.2rem;
		line-height: 1.6rem;
		text-align: center;
		letter-spacing: 0.490667px;
		text-transform: uppercase;
		color: #272727;
		margin-left: auto;
		margin-right: auto;
		// margin-top: -4.2rem;
		margin-bottom: 6.9rem;
