.button {
	@include flexcc;
	width: 23rem;
	background: #ffcc50;
	border-radius: 0.6rem;
	height: 4.7rem;
	font-size: 1.7rem;
	text-transform: uppercase;
	letter-spacing: 0.490667px;
	font-weight: bold;
	@include tr;
	color: #000;
	margin-left: auto;
	margin-right: auto;
	margin-top: 5rem;
	margin-bottom: 5rem;
	border:none;
	cursor:pointer;
	@media (max-width:573px) {
		font-size: 1.3rem;
	}
	&_margin{
		margin-top:0;
		margin-bottom:0;
	}
	&_big {
		width: 46rem;
		@media (max-width:573px) {
			width:100%;
		}
	}
	&_middle {
		width: 100%;
		height: 4.6rem;
		margin: 0;
		font-size: 1.2rem;
		line-height: 1.6rem;
	}
	&:hover {
		background: #ffb500;
	}
}
.select {
	display: inline-block;
	vertical-align: top;
	position: relative;
	width: 100%;
}

.select__gap {
	border: 1px solid #cfcfcf;
	border-radius: 1rem;
	background: #f9fdff;
	color: #8f9295;
	font-size: 1.2rem;
	padding: 1.2rem 1.5rem;
	line-height: 2rem;
	cursor: pointer;
	position: relative;
	z-index: 3;
	// height:4.6rem;
	@media (max-width:573px) {
		padding: 0.6rem 1.5rem;
	}
	&.bolder {
		font-weight: 500;
		color: #000;
	}
}

.on.select__gap,
.select__gap:hover {
	color: #000;
}

.select__list {
	background: transparent;
	margin: 0px 0;
	position: absolute;
	background: #f9fdff;
	border-radius: 0rem 0rem 1rem 1rem;
	z-index: 2;
	width: 100%;
	padding-top: 1rem;
	margin-top: -1rem;
}

.select__list.on {
	display: block;
}

.select__item {
	color: #8f9295;
	&:first-child {
		padding-top: 0.9rem;
		position: relative;
	}
	&:last-child {
		padding-bottom: 0.9rem;
	}
	span {
		display: block;
		padding: 1rem 1.5rem;
		cursor: pointer;

		font-size: 1.2rem;
		padding: 0.6rem 1.9rem;
		font-weight: 500;
	}
}

.select__item.selected,
.select__item span:hover {
	color: #000;
}

.select__gap:after {
	content: "";
	display: block;
	width: 0.9rem;
	height: 1.4rem;
	position: absolute;
	right: 1.7rem;
	top: 50%;
	background: url("../img/grey_arrow.svg") center center no-repeat;
	background-size: contain;
	margin-top: -0.7rem;
	-webkit-transition: all 0.27s ease-in-out;
	-o-transition: all 0.27s ease-in-out;
	transition: all 0.27s ease-in-out;
	-webkit-transform: rotate(0deg);
	-ms-transform: rotate(0deg);
	-o-transform: rotate(0deg);
	transform: rotate(0deg);
	@media (max-width:573px) {
		width: 0.6rem;
		height: 1rem;
		background-size:contain;
		margin-top:-0.5rem;
		right:1.2rem;
	}
}

.on.select__gap:after {
	-webkit-transform: rotate(-90deg);
	-ms-transform: rotate(-90deg);
	-o-transform: rotate(-90deg);
	transform: rotate(-90deg);
}
::-webkit-input-placeholder {
	font-family: "Gotham Pro";
	font-size: 1.2rem;
	line-height: 1.6rem;
	color: #c4c4c4;
}
::-moz-placeholder {
	font-family: "Gotham Pro";
	font-size: 1.2rem;
	line-height: 1.6rem;
	color: #c4c4c4;
}
:-ms-input-placeholder {
	font-family: "Gotham Pro";
	font-size: 1.2rem;
	line-height: 1.6rem;
	color: #c4c4c4;
}
:-moz-placeholder {
	font-family: "Gotham Pro";
	font-size: 1.2rem;
	line-height: 1.6rem;
	color: #c4c4c4;
}

.form_check {
	margin-bottom: 1.7rem;
	label {
		display: flex;
		// align-items: center;
	}
	.checkbox {
		display: none;
	}

	.checkbox-custom {
		position: relative;
		width: 2rem;
		height: 2rem;
		background: #cfcfcf;
		border-radius: 0.4rem;
		cursor: pointer;
		flex: none;
		margin-right: 1.7rem;
		@media (max-width:573px) {
			width: 1.6rem;
		height: 1.6rem;
		}
	}

	.checkbox-custom,
	.label {
		display: block;
	}

	.label {
		font-size: 0.9rem;
		line-height: 1.2rem;
	}
}

.checkbox:checked + .checkbox-custom::before {
	content: "";
	display: block;
	position: absolute;
	top: 2px;
	right: 2px;
	bottom: 2px;
	left: 2px;
	border-radius: 4px;
	background: url("../img/uil_check.png") center (center/contain) no-repeat #00b8ff;
	
}


.callback {
	padding: 2.3rem 1.6rem 2.8rem 1.6rem;
	border-radius: 1rem;
	&_blue {
		background: $blue;
		box-shadow: 0px 0px 1.8rem rgba(0, 0, 0, 0.07);
	}
	&_yellow {
		background: #fff8cc;
		box-shadow: 0px 0px 1.8rem rgba(0, 0, 0, 0.07);
	}
	&_violet {
		background: #d7d1ff;
		box-shadow: 0px 0px 1.8rem rgba(0, 0, 0, 0.07);
	}
	
	&__title {
			font-weight: bold;
			font-size: 1.5rem;
			line-height: 1.9rem;
			letter-spacing: 0.490667px;
			margin-bottom: 1.4rem;
		}
	&__subtitle {
			font-size: 1.5rem;
			line-height: 1.9rem;
			letter-spacing: 0.490667px;
			text-align: center;
			margin-bottom: 1.3rem;
		}
		
	&__input {
			background: #f9fdff;
			border: 1px solid #cfcfcf;
			border-radius: 0.6rem;
			width: 100%;
			height: 3.8rem;
			line-height: 3.8rem;
			margin-bottom: 0.6rem;
			padding: 0 1.2rem;
		}
		.select {
			margin-bottom: 0.6rem;
			.select__gap {
				border-radius: 0.6rem;
				padding: 0.8rem 1.2rem;
			}
			.select__list {
				border-radius: 0 0 0.6rem 0.6rem;
			}
		}

		button.button {
			width: 100%;
			height: 3.8rem;
			margin: 0;
			font-size: 1.2rem;
			line-height: 1.6rem;
		}
	
}

.big_form {
	padding: 4.8rem 0;
	position: relative;
	margin-bottom: 7rem;
	@media (max-width:573px) {
		padding: 3rem 0;
		margin-bottom:5rem;
		
	}

	&:before {
		content: "";
		width: 100vw;
		position: absolute;
		height: 100%;
		top: 0;
		left: 50%;
		margin-left: -50vw;
		@media (max-width:573px) {
			background-image:url("../img/footer_img.png");
		background-position:right bottom;
		background-repeat: no-repeat;
		}
	}
	&.blue {
		background-color: $blue;
		&:before {
			background-color: $blue;
		}
	}
	&.violet {
		background-color: $violet;
		&:before {
			background-color: $violet;
		}
	}
	&.yellow {
		background-color: #ffebbb;
		&:before {
			background-color: #ffebbb;
		}
	}
	.big_form_inner {
		display: flex;
		align-items: center;
		position: relative;
		z-index: 2;
		justify-content: space-between;
		@media (max-width:573px) {
			flex-wrap:wrap;
		}
		.big_form_left {
			width: 50%;
			@media (max-width:573px) {
				width:100%;
				img{
					display:none;
				}
			}
			.big_form_caption {
				font-weight: bold;
				font-size: 2.7rem;
				line-height: 3.4rem;
				letter-spacing: 0.490667px;
				margin-bottom: 2rem;
				@media (max-width:573px) {
					font-weight: bold;
					font-size: 1.8rem;
					line-height: 2.1rem;
					letter-spacing: 0.294px;
					margin-bottom:1.5rem;
				}
			}
			.big_form_text {
				font-size: 1.8rem;
				line-height: 2.5rem;
				letter-spacing: 0.490667px;
				margin-bottom: 2.3rem;
				@media (max-width:573px) {
					line-height: 2.1rem;
					letter-spacing: 0.2944px;

				}
			}

		}
		.big_form_right {
			width: 34%;
			@media (max-width:573px) {
				width:100%;
			}
			.form_block {
				margin-bottom: 1.6rem;
				label {
					font-size: 1.6rem;
					line-height: 1em;
					display: block;
					margin-bottom: 0.7rem;
					@media (max-width:573px) {
						font-size: 1.2rem;
						margin-bottom: 0.4rem;
					}
				}
				input[type="text"],
				input[type="email"],
				input[type="tel"] {
					width: 100%;
					background: #f9fdff;
					border: 1px solid #cfcfcf;
					border-radius: 1rem;
					height: 4.6rem;
					font-size: 1.3rem;
					line-height: 4.6rem;
					padding: 0 1.9rem;
					@media (max-width:573px) {
						height: 3.4rem;
						line-height: 3.4rem;
						padding: 0 1.5rem;
					}
				}
			}
			button[type="submit"] {
				margin-bottom: 1.4rem;
			}
			.form_info {
				display: flex;
				align-items: center;
				font-size: 0.9rem;
				line-height: 1.2rem;
				letter-spacing: 0.490667px;
				@media (max-width:573px) {
					letter-spacing: 0.2944px;
					max-width:25.7rem;
					font-style:italic;
				}
				img {
					flex: none;
					margin-right: 1.6rem;
					@media (max-width:573px) {
						display:none;
					}
				}
			}
		}
	}
}

.filter {
	margin-bottom: 5.5rem;
	@media (max-width: 573px) {
		margin-bottom:2.7rem;
	}
	form {
		display: grid;
		grid-template-columns: 1fr 1fr 1fr 1fr;
		grid-gap: 3rem;
		@media (max-width: 573px) {
			grid-template-columns: 1fr;
			grid-gap: 1rem;
		}
		.select__gap {
			padding-top: 0.9rem;
			padding-bottom: 0.9rem;
		}
	}
}

.form{
	.form-title{
		font-weight: bold;
		font-size: 1.9rem;
		line-height: 2.6rem;
		margin-bottom:1.2rem;
		@media (max-width:573px) {
			font-size: 1.2rem;
			line-height: 1.6rem;
		}
	}
	.form-subtitle{
		font-size: 1.8rem;
		line-height: 2.5rem;
		letter-spacing: 0.490667px;
		margin-bottom:2.3rem;
		@media (max-width:573px) {
			font-size: 1.1rem;
			line-height: 1.5rem;
		}
	}
	.form_check .label{
		font-size: 1.4rem;
		line-height: 1.8rem;
		@media (max-width:573px) {
			font-size: 0.9rem;
			line-height: 0.9rem;
		}
		
	}
	.form_check label{
		align-items:flex-start;
	}
	.form_block {
		margin-bottom: 1.6rem;
		@media (max-width:573px) {
			margin-bottom:1rem;
		}
		label {
			font-size: 1.6rem;
			line-height: 1em;
			display: block;
			margin-bottom: 0.7rem;
			@media (max-width:573px) {
				font-size: 1.2rem;
				margin-bottom: 0.4rem;
			}
		}
		input[type="text"],
		input[type="email"],
		input[type="tel"] {
			width: 100%;
			background: #f9fdff;
			border: 1px solid #cfcfcf;
			border-radius: 1rem;
			height: 4.6rem;
			font-size: 1.3rem;
			line-height: 4.6rem;
			padding: 0 1.9rem;
			@media (max-width:573px) {
				height: 2.8rem;
				line-height: 2.8rem;
				padding: 0 1.1rem;
				border-radius: 6px;
			}
		}
	}
	button.button {
		@media (max-width:573px) {
			margin-bottom: 1.4rem;
			font-size: 0.8rem;
			border-radius: 6px;
			height: 2.8rem;
		}
	}
	.form_info {
		display: flex;
		align-items: center;
		font-size: 1rem;
		line-height: 1.6rem;
		letter-spacing: 0.490667px;
		@media (max-width:573px) {
			letter-spacing: 0.2944px;
			
			font-style:italic;
			font-size: 0.6rem;
			line-height: 1rem;
			font-style: normal;
		}
		img {
			flex: none;
			margin-right: 1.6rem;
			@media (max-width:573px) {
				display:none;
			}
		}
	}
}
