*,
*::after,
*::before {
    box-sizing: border-box;
}
html{
    font-size:10px;
    
    @media (max-width:1146px) {
       font-size:9px;
    } 
    @media (max-width:1031px) {
       font-size:8px;
    } 
    @media (max-width:916px) {
       font-size:5px;
    } 
    @media (max-width:573px) {
        font-size:10px;
       
    } 
   
}
blockquote,
body,
dd,
dl,
figcaption,
figure,
h1,
h2,
h3,
h4,
li,
ol,
p,
ul {
    margin: 0;
}

body {
    font-family: 'Gotham Pro';
    font-weight: 400;
    scroll-behavior: smooth;
    text-rendering: optimizeSpeed;
    overflow-x: hidden;
    color: #000;
    height: 100%;
    font-size: 1.6rem;
    line-height: 1.9rem;
    background: #FAFAFA;
    &.single-blog{
        background: #fff;
    }
    @media (max-width:573px) {
        padding-top:10.4rem;
    }
}

ol,
ul {
    padding: 0;
    list-style: none;
}

a {
    font-family: inherit;
    text-decoration: none;
    color: inherit;
}

img {
    max-width: 100%;
    height: auto;
}

button {
    cursor: pointer;
    border: none;
}

button,
input,
select,
textarea {
    font-family: inherit;
}
@mixin tr{
    -webkit-transition: all .25s ease;
    transition: all .25s ease;
}
@mixin ulClean{
    list-style:none;
    padding:0;
    margin:0;
}
@mixin flexcc{
    display:flex;
    justify-content: center;
    align-items:center;
}
:active, :focus {
    outline: none !important;
}
::-moz-focus-inner {
    border: 0 !important;
}
.videoWrapper {
	position: relative;
  padding-bottom: 56.25%; /* 16:9 */
  height: 0;
}
.videoWrapper iframe {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}
.container{
	width:114.6rem;
	margin:0 auto;
	@media (max-width:573px) {
		width:100%;
        padding-left:15px;
        padding-right:15px;
	}
}
.owl-carousel .owl-item img{
	width:auto;
	display:inline-block;
}
.owl-carousel.off {
    display: block;
}